.o4liXq_pluginWrap {
  height: 41px;
  align-items: center;
  margin: 0 1rem;
  display: flex;
  font-size: .8rem !important;
}

.o4liXq_toggleButton {
  background-color: var(--grayLight);
  border: 1px solid var(--grayMedium);
  color: var(--textSubtleLight);
  -webkit-user-select: none;
  user-select: none;
  border-radius: 6px;
  align-items: center;
  gap: .5em;
  padding: 2px .3rem 2px .5rem;
  transition: all .1s ease-in-out;
  display: flex;
}

.o4liXq_toggleButton:hover {
  background-color: var(--grayLighter);
  color: var(--textOnWhite);
}

.o4liXq_buttonIcon {
  background-color: var(--grayMedium);
  color: var(--textOnGray);
  border-radius: 3px;
  padding: 1px 4px;
  font-size: 80%;
}

